body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.react-checkbox-tree {
	font-size: 16px;
 }

 .react-checkbox-tree ol {
	margin: 0;
	padding-left: 0;
	list-style-type: none;
 }

 .react-checkbox-tree ol ol {
	padding-left: 24px;
 }

 .react-checkbox-tree button {
	line-height: normal;
	color: inherit;
 }

 .react-checkbox-tree button:focus {
	outline: none;
 }

 .react-checkbox-tree button:disabled {
	cursor: not-allowed;
 }

 .react-checkbox-tree label {
	margin-bottom: 0;
	cursor: pointer;
 }

 .react-checkbox-tree label:hover {
	background: rgba(51, 51, 204, 0.1);
 }

 .react-checkbox-tree label:active {
	background: rgba(51, 51, 204, 0.15);
 }

 .react-checkbox-tree input {
	display: none;
 }

 .react-checkbox-tree .rct-icon {
	font-family: "FontAwesome";
	font-style: normal;
 }

 .rct-disabled > .rct-text > label {
	opacity: .75;
	cursor: not-allowed;
 }

 .rct-disabled > .rct-text > label:hover {
	background: transparent;
 }

 .rct-disabled > .rct-text > label:active {
	background: transparent;
 }

 .rct-collapse,
 .rct-checkbox,
 .rct-node-icon {
	padding: 0 5px;
 }

 .rct-collapse *,
 .rct-checkbox *,
 .rct-node-icon * {
	display: inline-block;
	margin: 0;
	width: 14px;
 }

 .rct-text {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
 }

 .rct-collapse {
	border: 0;
	background: none;
	line-height: normal;
	color: inherit;
	font-size: 12px;
 }

 .rct-collapse.rct-collapse-btn {
	cursor: pointer;
 }

 .rct-collapse > .rct-icon-expand-close {
	opacity: .5;
 }

 .rct-collapse > .rct-icon-expand-close:hover {
	opacity: 1;
 }

 .rct-node-icon {
	color: #33c;
 }

 .rct-title {
	padding: 0 5px;
 }

 .rct-icon-expand-close::before {
	content: "\f054";
 }

 .rct-icon-expand-open::before {
	content: "\f078";
 }

 .rct-icon-uncheck::before {
 content: "\f096";
 }

 .rct-icon-check::before {
	content: "\f046";
 }

 .rct-icon-half-check::before {
	opacity: .5;
	content: "\f046";
 }

 .rct-icon-leaf::before {
	content: "\f016";
 }

 .rct-icon-parent-open::before {
	content: "\f115";
 }

 .rct-icon-parent-close::before {
	content: "\f114";
 }

