 body {
	padding: 0;
	margin: 0;
	font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: 16px;
	line-height: 1.5;
	color: #606c71;
 }

 a {
	color: #1e6bb8;
	text-decoration: none;
 }

 a:hover {
	text-decoration: underline;
 }

 .btn {
	display: inline-block;
	margin-bottom: 1rem;
	color: rgba(255, 255, 255, 0.7);
	background-color: rgba(255, 255, 255, 0.08);
	border-color: rgba(255, 255, 255, 0.2);
	border-style: solid;
	border-width: 1px;
	border-radius: 0.3rem;
	-webkit-transition: color 0.2s, background-color 0.2s, border-color 0.2s;
	-o-transition: color 0.2s, background-color 0.2s, border-color 0.2s;
	transition: color 0.2s, background-color 0.2s, border-color 0.2s;
 }

 .btn:hover {
	color: rgba(255, 255, 255, 0.8);
	text-decoration: none;
	background-color: rgba(255, 255, 255, 0.2);
	border-color: rgba(255, 255, 255, 0.3);
 }

 .btn + .btn {
	margin-left: 1rem;
 }

 @media screen and (min-width: 64em) {
	.btn {
	padding: 0.75rem 1rem;
	}
 }

 @media screen and (min-width: 42em) and (max-width: 64em) {
	.btn {
	padding: 0.6rem 0.9rem;
	font-size: 0.9rem;
	}
 }

 @media screen and (max-width: 42em) {
	.btn {
	display: block;
	width: 100%;
	padding: 0.75rem;
	font-size: 0.9rem;
	}
	.btn + .btn {
	margin-top: 1rem;
	margin-left: 0;
	}
 }

 .page-header {
	color: #fff;
	text-align: center;
	background-color: #3498db;
	background-image: -webkit-linear-gradient(330deg, #2c3e50, #3498db);
	background-image: -o-linear-gradient(330deg, #2c3e50, #3498db);
	background-image: linear-gradient(120deg, #2c3e50, #3498db);
 }

 @media screen and (min-width: 64em) {
	.page-header {
	padding: 5rem 6rem;
	}
 }

 @media screen and (min-width: 42em) and (max-width: 64em) {
	.page-header {
	padding: 3rem 4rem;
	}
 }

 @media screen and (max-width: 42em) {
	.page-header {
	padding: 2rem 1rem;
	}
 }

 .project-name {
	margin-top: 0;
	margin-bottom: 0.1rem;
 }

 @media screen and (min-width: 64em) {
	.project-name {
	font-size: 3.25rem;
	}
 }

 @media screen and (min-width: 42em) and (max-width: 64em) {
	.project-name {
	font-size: 2.25rem;
	}
 }

 @media screen and (max-width: 42em) {
	.project-name {
	font-size: 1.75rem;
	}
 }

 .project-tagline {
	margin-bottom: 2rem;
	font-weight: normal;
	opacity: 0.7;
 }

 @media screen and (min-width: 64em) {
	.project-tagline {
	font-size: 1.25rem;
	}
 }

 @media screen and (min-width: 42em) and (max-width: 64em) {
	.project-tagline {
	font-size: 1.15rem;
	}
 }

 @media screen and (max-width: 42em) {
	.project-tagline {
	font-size: 1rem;
	}
 }

 .main-content {
	word-wrap: break-word;
 }

 .main-content :first-child {
	margin-top: 0;
 }

 @media screen and (min-width: 64em) {
	.main-content {
	max-width: 64rem;
	padding: 2rem 6rem;
	margin: 0 auto;
	font-size: 1.1rem;
	}
 }

 @media screen and (min-width: 42em) and (max-width: 64em) {
	.main-content {
	padding: 2rem 4rem;
